      discountProdects: dataDiscount,
      newPraducts: dataNew,
    };
  },
  created() {
    this.imgLoad();
  },
  methods: {
    imgLoad() {
      this.$nextTick(() => {
        this.bannerHeight = this.$refs.imgHeight[0].height;
      });
    },
  },
  mounted() {
    window.addEventListener(
      "resize",
      () => {
        this.bannerHeight = this.$refs.imgHeight[0].height;
      },
      false
    );
  },
};
